<template>
  <div>
    <h2 class="subtitle">{{title}}</h2>
      {{$t("draw")}} #{{number}}<br>
      <div v-if='draw'>
        <div class="level-item">
          {{$t("participants")}}:
          {{draw.participantCounter}}
        </div>
        <div class="level-item">
          <!-- <div v-if='!draw.closed'>Fund amount:<br>
          {{$eth.fromWei(draw.fundAmount)}} GOGO</div> -->
          <div>{{$t("prizeAmount")}}:  {{$eth.fromWei(draw.fundAmount)}} GOGO</div>
        </div><br>
        <div v-if='draw.closed'>
          <div class="level-item" v-if='draw.firstWinner !== "0x0000000000000000000000000000000000000000"'>
            <b-icon
              icon="numeric-1-box-outline"
              size="is-small">
            </b-icon>
            <!-- <b-icon
              icon="star"
              size="is-small">
            </b-icon>
            <b-icon
              icon="star"
              size="is-small">
            </b-icon> -->
            <span class="eth-address">{{draw.firstWinner}}</span>
          </div>
          <div class="level-item" v-if='draw.secondWinner !== "0x0000000000000000000000000000000000000000"'>
            <b-icon
              icon="numeric-2-box-outline"
              size="is-small">
            </b-icon>
            <!-- <b-icon
              icon="star"
              size="is-small">
            </b-icon> -->
            <span class="eth-address">{{draw.secondWinner}}</span>
          </div>
          <div class="level-item" v-if='draw.thirdWinner !== "0x0000000000000000000000000000000000000000"'>
            <b-icon
              icon="numeric-3-box-outline"
              size="is-small">
            </b-icon>
            <span class="eth-address">{{draw.thirdWinner}}</span>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
export default {
  props: ['title', 'number', 'draw', 'lang'],
  i18n: {
    messages: {
      en: {
        draw: 'Lotto',
        participants: 'Participants',
        prizeAmount: 'Prize amount'
      },
      ko: {
        draw: '',
        participants: ' ',
        prizeAmount: ''
      }
    }
  },
  watch: {
    lang () {
      this.$i18n.locale = this.lang
    }
  }
}
</script>



