<template>
  <div style="display: inline-block;">
    <h2 class="subtitle">{{title}}</h2>
    <div class="pie-wrapper style-2">
      <span class="label">{{label}}</span>
      <div :style='pieStyle'>
        <div :style='leftStyle'></div>
        <div :style='rightStyle'></div>
      </div>
      <div class="shadow"></div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['prog', 'label', 'title'],
  computed: {
    pieStyle () {
      return this.prog > 50
      ? {
        clip: 'rect(auto, auto, auto, auto)',
        width: '100%',
        height: '100%',
        left: '0',
        position: 'absolute',
        top: '0'
      }
      : {
        clip: 'rect(0, 1em, 1em, 0.5em)',
        width: '100%',
        height: '100%',
        left: '0',
        position: 'absolute',
        top: '0'
      }
    },
    leftStyle () {
      return this.prog > 50
      ? {
        transform: `rotate(` + this.prog * 3.6 + `deg)`,

        width: '100%',
        height: '100%',
        border: '0.1em solid #3498db',
        'border-radius': '50%',
        clip: 'rect(0, 0.5em, 1em, 0)',
        left: '0',
        top: '0',
        position: 'absolute'
      }
      : {
        transform: `rotate(` + this.prog * 3.6 + `deg)`,

        width: '100%',
        height: '100%',
        border: '0.1em solid #3498db',
        'border-radius': '50%',
        clip: 'rect(0, 0.5em, 1em, 0)',
        left: '0',
        top: '0',
        position: 'absolute'
      }
    },
    rightStyle () {
      return this.prog > 50
      ? {
        transform: 'rotate(180deg)',
        width: '100%',
        height: '100%',
        border: '0.1em solid #3498db',
        'border-radius': '50%',
        clip: 'rect(0, 0.5em, 1em, 0)',
        left: '0',
        top: '0',
        position: 'absolute'
      }
      : {
        display: 'none'
      }
    }
  }
}
</script>


<style lang="less" scoped>
.size(@w, @h) {
  height: @h;
  width: @w;
}

@bg: #34495e;
@size: 1em;

.pie-wrapper {
  .size(@size, @size);
  font-size: 10em;
  margin: 15px;
  position: relative;
  display: inline-block;
  // position: relative;

  .label {
    @font-size: @size / 10;
    @font-size-redo: @size * 4;

    background: @bg;
    border-radius: 50%;
    bottom: @font-size-redo / 10;
    color: #ecf0f1;
    cursor: default;
    font-size: @font-size;
    left: @font-size-redo / 10;
    line-height: @font-size-redo * .65;
    position: absolute;
    right: @font-size-redo / 10;
    text-align: center;
    top: 35%;

    .smaller {
      color: #bdc3c7;
      font-size: .45em;
      padding-bottom: 20px;
      // vertical-align: super;
    }
  }

  .shadow {
    .size(100%, 100%);
    border: @size / 10 solid #bdc3c7;
    border-radius: 50%;
  }

  &.style-2 {
    .label {
      background: none;
      color: #7f8c8d;

      .smaller {
        color: #bdc3c7;
      }
    }
  }
}
</style>



