<template>
  <div>
    <h2 class="subtitle">{{$t("instructions")}}</h2>
    <span v-html='$t("firstStep")'></span>
    <div class="level-item">
      <span>
      <b-icon
        icon="currency-eth"
        size="is-small">
      </b-icon>
      <b-icon
        icon="arrow-right"
        size="is-small">
      </b-icon>
      <b-icon
        icon="ticket"
        size="is-small">
      </b-icon>
      <b class="eth-address">{{routerAddress}}</b>
      </span>
    </div><br>
    <span v-html='$t("secondStep")'></span>
    <div class="level-item">
      <span>
      <b-icon
        icon="ticket"
        size="is-small">
      </b-icon>
      <b-icon
        icon="arrow-right"
        size="is-small">
      </b-icon>
      <b-icon
        icon="dice-multiple"
        size="is-small">
      </b-icon>
      <b class="eth-address">{{tokenAddress}}</b>
      </span>
    </div><br>
    <span v-html='$t("thirdStep")'></span>
    <div class="level-item">
      <span>
      <b-icon
        icon="currency-eth"
        size="is-small">
      </b-icon>
      <b-icon
        icon="arrow-left"
        size="is-small">
      </b-icon>
      <b-icon
        icon="ticket"
        size="is-small">
      </b-icon>
      <b class="eth-address">{{routerAddress}}</b>
      </span>
    </div>
  </div>
</template>

<script>
export default {
  props: ['routerAddress', 'tokenAddress', 'lang'],
  i18n: {
    messages: {
      en: {
        instructions: 'Participation info',
        firstStep: 'To buy tokens <b>send ethers</b> to:<br>',
        secondStep: 'To play you need <b>transfer tokens</b> to:<br>',
        thirdStep: 'To convert your tokens in ethers <b>transfer tokens</b> to:<br>'
      },
      ko: {
        instructions: ' ',
        firstStep: '      ',
        secondStep: '      .',
        thirdStep: '   ,    .'
      }
    }
  },
  watch: {
    lang () {
      this.$i18n.locale = this.lang
    }
  }
}
</script>




