<template>
  <div class="container">
    <!-- <div class="level"> -->
      <!-- <div class="level-right">
        
      </div> -->
      <div class="level">
        <!-- <div class="level-right"> -->
          <b-switch v-model="isKorean"></b-switch>
          <a class="button is-danger"
            @click="isInstructuionsModalActive = true">
            Instructions
          </a>
          <b-modal :active.sync="isInstructuionsModalActive" :width="640" scroll="keep">
            <div class="card">
              <div class="card-content">
                <div class="content" v-html='$t("howToPlay")'>
                </div>
              </div>
            </div>
          </b-modal>
        </div>
      <!-- </div> -->
    <!-- </div> -->
    <div class="box">
      <h1 class="title">
        <div class="level">
          <div class="level-item">
            <span>{{ $t('toDailyDrawingRemains') }}: {{leftTime}}</span>
          </div>
          <div class="level-item">
            <span v-if='daily'>{{ $t('currentPrize') }}: {{$eth.fromWei(daily.fundAmount)}} GOGO</span>
          </div>
        </div>
      </h1>
    </div>
    <section>
      <radial-progress :prog='toWeeklyPerc' :label='timeToWeekly' :title='$t("weeklyLotto")'></radial-progress>
      <radial-progress :prog='toMonthlyPerc' :label='timeToMonthly' :title='$t("monthlyLotto")'></radial-progress>
      <radial-progress :prog='toAnnualPerc' :label='timeToAnnual' :title='$t("annualLotto")'></radial-progress>
    </section>
    <section class="box">
      <b-tabs position="is-centered" class="block">
        <b-tab-item :label='$t("dailyLotto")'>
          <div class="columns">
              <div class="column">
              <draw
                :title='$t("pastLotto")'
                :number='pastDailyNumber'
                :draw='pastDaily'
                :lang='lang'>
              </draw>
            </div>
            <!-- <div class="column">
              <draw
                :title='$t("currentStatistics")'
                :number='dailyNumber'
                :draw='daily'
                :lang='lang'>
              </draw>
            </div> -->
            <div class="column">
              <instructions 
                :routerAddress='routerAddress'
                :tokenAddress='tokenAddress'
                :lang='lang'>
              </instructions>
            </div>
          </div>
        </b-tab-item>
        <b-tab-item :label='$t("weeklyLotto")'>
          <div class="columns">
            <div class="column">
              <draw
                :title='$t("pastLotto")'
                :number='pastWeeklyNumber'
                :draw='pastWeekly'
                :lang='lang'>
              </draw>
            </div>
            <div class="column">
              <draw
                :title='$t("currentStatistics")'
                :number='weeklyNumber'
                :draw='weekly'
                :lang='lang'>
              </draw>
            </div>
            <div class="column">
              <div>
                <h2 class="subtitle">{{$t("instructions")}}</h2>
                <div>{{ $t("shortInstruction") }}</div>
              </div>
            </div>
          </div>
        </b-tab-item>
        <b-tab-item :label='$t("monthlyLotto")'>
          <div class="columns">
            <div class="column">
              <draw
                :title='$t("pastLotto")'
                :number='pastMonthlyNumber'
                :draw='pastMonthly'
                :lang='lang'>
              </draw>
            </div>
            <div class="column">
              <draw
                :title='$t("currentStatistics")'
                :number='monthlyNumber'
                :draw='monthly'
                :lang='lang'>
              </draw>
            </div>
            <div class="column">
              <div>
                <h2 class="subtitle">{{$t("instructions")}}</h2>
                <div>{{ $t("shortInstruction") }}</div>
              </div>
            </div>
          </div>
        </b-tab-item>
        <b-tab-item :label='$t("annualLotto")'>
          <div class="columns">
            <div class="column">
              <draw
                :title='$t("pastLotto")'
                :number='pastAnnualNumber'
                :draw='pastAnnual'
                :lang='lang'>
              </draw>
            </div>
            <div class="column">
              <draw
                :title='$t("currentStatistics")'
                :number='annualNumber'
                :draw='annual'
                :lang='lang'>
              </draw>
            </div>
            <div class="column">
              <div>
                <h2 class="subtitle">{{$t("instructions")}}</h2>
                <div>{{ $t("shortInstruction") }}</div>
              </div>
            </div>
          </div>
        </b-tab-item>
      </b-tabs>
    </section>
    <section class="box">
      <div class="level-item">
        <b-icon
          icon="arrow-down"
          size="is-medium">
        </b-icon>&nbsp;
        <h2 class="subtitle">{{$t("checkYourBets")}}</h2>&nbsp;
        <b-icon
          icon="arrow-down"
          size="is-medium">
        </b-icon>
      </div>
      {{$t("insertYourAddress")}}
      <b-field>
        <b-input
          type="search"
          icon="magnify"
          v-model='searchAddress'
          @input='search'
          :placeholder='$t("searchPlaceholder")'>
        </b-input>
      </b-field>
      <search-results :tokenBalance='searchBalance' :tableData='searchData' :lang='lang'></search-results>
      <b-loading :active.sync="isLoading" :canCancel="true"></b-loading>
    </section>
  </div>
</template>

<script>
import radialProgress from '@/components/RadialProgress'
import Instructions from '@/components/Instructions'
import Draw from '@/components/Draw'
import SearchResults from '@/components/SearchResults'
import moment from 'moment-timezone'
export default {
  i18n: {
    messages: {
      en: {
        hello: 'hello!',
        toDailyDrawingRemains: 'Until daily lotto',
        currentPrize: 'Prize Pool',
        weeklyLotto: 'Weekly Game',
        monthlyLotto: 'Monthly Game',
        annualLotto: 'The Annual Game',
        dailyLotto: 'Daily Game',
        pastLotto: 'Last lottery',
        currentStatistics: 'Current Statistics',
        instructions: 'Participation info',
        shortInstruction: 'You participate in this lotto automaticaly if you take in daily lotto',
        checkYourBets: 'Check your bets',
        insertYourAddress: 'insert your ethereum address below',
        searchPlaceholder: 'Search...',
        howToPlay: `How to play: <br>
        <ol>
          <li>Go to: https://gogolotto.github.io</li>
          <li>Send ETH to cashdesk address, and it will return GOGO tokens.</li>
          <li>Once add GOGO tokens (custom token contract address 0x2D52cA92B2a3f60EF6DcAA248C4aB1d8a6021E7d , token GOGO, decimals 18)</li>
          <li>Send to GOGO tokens to lotto address, and become a player in daily lotto and weekly+monthly+annual Jackpot.</li>
          <li>Winner gets 50% of funds, 2nd place  30%, 3rd place  20%. Plus, some bonus goes to the team.</li>
          <li>Winners get the prize in GOGO tokens.</li>
          <li>You can exchange GOGO to ETH in cashdesk (but with 25% discount), or you can play one more time to win more.</li>
        </ol>`
      },
      ko: {
        hello: '.',
        toDailyDrawingRemains: '    ',
        currentPrize: '',
        weeklyLotto: ' ',
        monthlyLotto: ' ',
        annualLotto: ' ',
        dailyLotto: ' ',
        pastLotto: ' ',
        currentStatistics: '  ',
        instructions: ' ',
        shortInstruction: '        ',
        checkYourBets: '  !',
        insertYourAddress: '    ',
        searchPlaceholder: '...',
        howToPlay: `How to play:<br>
        <ol>
          <li>https://gogolotto.github.io  </li>
          <li>Cashdesk   , GOGO  .</li>
          <li>GOGO    . (custom token contract address 0x2D52cA92B2a3f60EF6DcAA248C4aB1d8a6021E7d , token GOGO, decimals 18)</li>
          <li>GOGO     ///     .</li>
          <li>1   50%, 2 30% , 3 20%  .     .</li>
          <li> GOGO  .</li>
          <li> GOGO      ( 25%  .)   GOGO       .</li>
        </ol>`
      }
    }
  },
  data () {
    return {
      isKorean: false,
      isInstructuionsModalActive: false,
      leftTime: '',
      toWeeklyPerc: 0,
      toMonthlyPerc: 0,
      toAnnualPerc: 0,
      daysToWeekly: 0,
      daysToMonthly: 0,
      daysToAnnual: 0,
      routerAddress: this.$eth.routerAddress(),
      tokenAddress: this.$eth.tokenAddress(),
      dailyNumber: 0,
      daily: null,
      pastDailyNumber: 0,
      pastDaily: null,
      weeklyNumber: 0,
      weekly: null,
      pastWeeklyNumber: 0,
      pastWeekly: null,
      monthlyNumber: 0,
      monthly: null,
      pastMonthlyNumber: 0,
      pastMonthly: null,
      annualNumber: 0,
      annual: null,
      pastAnnualNumber: 0,
      pastAnnual: null,
      searchAddress: '',
      searchBalance: '',
      searchData: [],
      isLoading: false
    }
  },
  computed: {
    lang () {
      return this.isKorean ? 'ko' : 'en'
    },
    timeToWeekly () {
      let diff = this.daysToWeekly
      if (this.isKorean) {
        return diff > 0 ? diff + '  ' : ''
      } else {
        return diff > 0 ? 'in ' + diff + ' days' : 'Today'
      }
    },
    timeToMonthly () {
      let diff = this.daysToMonthly
      if (this.isKorean) {
        return diff > 0 ? diff + '  ' : ''
      } else {
        return diff > 0 ? 'in ' + diff + ' days' : 'Today'
      }
    },
    timeToAnnual () {
      let diff = this.daysToAnnual
      if (this.isKorean) {
        return diff > 0 ? diff + '  ' : ''
      } else {
        return diff > 0 ? 'in ' + diff + ' days' : 'Today'
      }
    }
  },
  watch: {
    isKorean () {
      this.$i18n.locale = this.lang
    }
  },
  methods: {
    async search () {
      if (this.$eth.isAddress(this.searchAddress)) {
        this.searchBalance = await this.$eth.balanceOf(this.searchAddress)
        this.searchParticipation(this.searchAddress)
      } else {
        this.$toast.open({
          duration: 1000,
          message: `Invalid address`,
          position: 'is-bottom',
          type: 'is-danger'
        })
      }
    },
    async searchParticipation (addr) {
      this.searchData = []
      this.isLoading = true
      try {
        for (let i = this.dailyNumber; i >= 0; --i) {
          let daily = await this.$eth.daily(i)
          for (let j = daily.participantCounter - 1; j >= 0; --j) {
            let participant = await this.$eth.dailyParticipant(i, j)
            if (addr.toLowerCase() === participant.toLowerCase()) {
              let status = (daily.firstWinner === participant ||
                daily.secondWinner === participant ||
                daily.thirdWinner === participant) &&
                participant !== '0x0000000000000000000000000000000000000000' && daily.closed ? 'win' : 'loss'
              if (!daily.closed) {
                status = 'active'
              }
              this.searchData.push({address: addr, drawNumber: i, drawType: 'daily', status})
            }
          }
        }
        for (let i = this.weeklyNumber; i >= 0; --i) {
          let weekly = await this.$eth.weekly(i)
          for (let j = weekly.participantCounter - 1; j >= 0; --j) {
            let participant = await this.$eth.weeklyParticipant(i, j)
            if (addr.toLowerCase() === participant.toLowerCase()) {
              let status = (weekly.firstWinner === participant ||
                weekly.secondWinner === participant ||
                weekly.thirdWinner === participant) &&
                participant !== '0x0000000000000000000000000000000000000000' && weekly.closed ? 'win' : 'loss'
              if (!weekly.closed) {
                status = 'active'
              }
              this.searchData.push({address: addr, drawNumber: i, drawType: 'weekly', status})
            }
          }
        }
        for (let i = this.monthlyNumber; i >= 0; --i) {
          let monthly = await this.$eth.monthly(i)
          for (let j = monthly.participantCounter - 1; j >= 0; --j) {
            let participant = await this.$eth.monthlyParticipant(i, j)
            if (addr.toLowerCase() === participant.toLowerCase()) {
              let status = (monthly.firstWinner === participant ||
                monthly.secondWinner === participant ||
                monthly.thirdWinner === participant) &&
                participant !== '0x0000000000000000000000000000000000000000' && monthly.closed ? 'win' : 'loss'
              if (!monthly.closed) {
                status = 'active'
              }
              this.searchData.push({address: addr, drawNumber: i, drawType: 'monthly', status})
            }
          }
        }
        for (let i = this.annualNumber; i >= 0; --i) {
          let annual = await this.$eth.annual(i)
          for (let j = annual.participantCounter - 1; j >= 0; --j) {
            let participant = await this.$eth.annualParticipant(i, j)
            if (addr.toLowerCase() === participant.toLowerCase()) {
              let status = (annual.firstWinner === participant ||
                annual.secondWinner === participant ||
                annual.thirdWinner === participant) &&
                participant !== '0x0000000000000000000000000000000000000000' && annual.closed ? 'win' : 'loss'
              if (!annual.closed) {
                status = 'active'
              }
              this.searchData.push({address: addr, drawNumber: i, drawType: 'annual', status})
            }
          }
        }
      } catch (err) {
        console.log(err)
      }
      this.$scrollTo('#search-results')
      this.isLoading = false
    },
    async loadAnnual () {
      this.annualNumber = await this.$eth.annualCounter()
      this.annual = await this.$eth.annual(this.annualNumber)
      if (this.annualNumber > 0) {
        await this.loadPastAnnual(this.annualNumber - 1)
      }
    },
    async loadPastAnnual (number) {
      try {
        this.pastAnnualNumber = number
        this.pastAnnual = await this.$eth.annual(number)
        console.log()
      } catch (err) {
        console.log(err)
      }
    },
    async loadMonthly () {
      this.monthlyNumber = await this.$eth.monthlyCounter()
      this.monthly = await this.$eth.monthly(this.monthlyNumber)
      if (this.monthlyNumber > 0) {
        await this.loadPastMonthly(this.monthlyNumber - 1)
      }
    },
    async loadPastMonthly (number) {
      try {
        this.pastMonthlyNumber = number
        this.pastMonthly = await this.$eth.monthly(number)
        console.log()
      } catch (err) {
        console.log(err)
      }
    },
    async loadWeekly () {
      this.weeklyNumber = await this.$eth.weeklyCounter()
      this.weekly = await this.$eth.weekly(this.weeklyNumber)
      if (this.weeklyNumber > 0) {
        await this.loadPastWeekly(this.weeklyNumber - 1)
      }
    },
    async loadPastWeekly (number) {
      try {
        this.pastWeeklyNumber = number
        this.pastWeekly = await this.$eth.weekly(number)
        console.log()
      } catch (err) {
        console.log(err)
      }
    },
    async loadDaily () {
      this.dailyNumber = await this.$eth.dailyCounter()
      this.daily = await this.$eth.daily(this.dailyNumber)
      if (this.dailyNumber > 0) {
        await this.loadPastDaily(this.dailyNumber - 1)
      }
    },
    async loadPastDaily (number) {
      try {
        this.pastDailyNumber = number
        this.pastDaily = await this.$eth.daily(number)
        console.log()
      } catch (err) {
        console.log(err)
      }
    },
    async loadETH () {
      this.isLoading = true
      try {
        await this.$eth.load()
        await this.loadDaily()
        await this.loadWeekly()
        await this.loadMonthly()
        await this.loadAnnual()
      } catch (err) {
        console.log(err)
      }
      this.isLoading = false
    },
    startBackCounter () {
      setInterval(() => {
        let now = moment().clone()
        let koreanTime = now.tz('Asia/Tokyo')
        let endDay = (koreanTime.hours() > 20) ||
          (koreanTime.hours() === 20 && koreanTime.minutes() >= 30) ? koreanTime.clone().add(1, 'day') : koreanTime
        let drawTime = moment(endDay.format('YYYY-MM-DD') + ' 20:30:00.000+09:00')
        let diff = drawTime.diff(koreanTime)
        let hours = Math.floor(diff / 1000 / 3600)
        diff -= hours * 3600 * 1000
        let minutes = Math.floor(diff / 1000 / 60)
        diff -= minutes * 60 * 1000
        let seconds = Math.floor(diff / 1000)
        this.leftTime = (hours < 10 ? '0' + hours : hours) +
          ':' + (minutes < 10 ? '0' + minutes : minutes) +
          ':' + (seconds < 10 ? '0' + seconds : seconds)

        let sunday = (koreanTime.isoWeekday() !== 7) ||
          (koreanTime.hours() > 20) ||
          (koreanTime.hours() === 20 && koreanTime.minutes() >= 45) ? koreanTime.clone().day(7) : koreanTime
        let weeklyTime = moment.parseZone(sunday.format('YYYY-MM-DD') + 'T20:45:00+09:00')
        diff = weeklyTime.diff(koreanTime, 'days')
        this.daysToWeekly = diff
        this.toWeeklyPerc = (1 - diff / 7) * 100

        let firstDate = (koreanTime.date() !== 1) ||
          (koreanTime.hours() > 21) ||
          (koreanTime.hours() === 21 && koreanTime.minutes() >= 0) ? koreanTime.clone().date(1).add(1, 'month') : koreanTime
        let monthlyTime = moment.parseZone(firstDate.format('YYYY-MM-DD') + 'T21:00:00+09:00')
        diff = monthlyTime.diff(koreanTime, 'days')
        this.daysToMonthly = diff
        this.toMonthlyPerc = (1 - diff / 31) * 100

        let annualDate = moment()
        if (koreanTime.date() !== 31 || koreanTime.month() !== 11) {
          annualDate = koreanTime.clone().month(11).date(31)
        } else if ((koreanTime.hours() > 21) ||
          (koreanTime.hours() === 21 && koreanTime.minutes() >= 30)) {
          annualDate = koreanTime.clone().add(1, 'year').month(11).date(31)
        }

        let annualTime = moment.parseZone(annualDate.format('YYYY-MM-DD') + 'T21:30:00+09:00')
        diff = annualTime.diff(koreanTime, 'days')
        this.daysToAnnual = diff
        this.toAnnualPerc = (1 - diff / 365) * 100
      }, 1000)
    }
  },
  created () {
    try {
      this.isKorean = navigator.language.substring(0, 2) === 'ko'
      this.loadETH()
      this.startBackCounter()
    } catch (err) {
      console.log(err)
    }
  },
  components: {
    Instructions,
    Draw,
    SearchResults,
    radialProgress
  }
}
</script>



