<template>
  <div id="search-results">
    <div v-if='tokenBalance !== ""'>
      {{ $t("tokenBalance") }}: {{tokenBalance}} GOGO
    </div>
    <b-table
      :data="isEmpty ? [] : tableData"
      :bordered="isBordered"
      :striped="isStriped"
      :narrowed="isNarrowed"
      :loading="isLoading"
      :mobile-cards="hasMobileCards">
      <template slot-scope="props">
        <b-table-column :label='$t("address")'>
          <span class="eth-address">{{ props.row.address }}</span>
        </b-table-column>
        <b-table-column :label='$t("draw") + " #"' centered>
          {{ props.row.drawNumber }}
        </b-table-column>
        <b-table-column :label='$t("type")' centered>
          {{ props.row.drawType }}
        </b-table-column>
        <b-table-column :label='$t("status")' centered>
          <b-tag :type='switchTypeResultRow(props.row)' rounded>{{ props.row.status }}</b-tag>
        </b-table-column>
      </template>
      </b-table>
  </div>
</template>

<script>
export default {
  props: ['tokenBalance', 'tableData', 'lang'],
  i18n: {
    messages: {
      en: {
        tokenBalance: 'Token balance',
        draw: 'Lotto',
        address: 'Address',
        type: 'Type',
        status: 'Status'
      },
      ko: {
        tokenBalance: '  ',
        draw: '',
        address: '',
        type: '',
        status: ''
      }
    }
  },
  watch: {
    lang () {
      this.$i18n.locale = this.lang
    }
  },
  data () {
    return {
      isEmpty: false,
      isBordered: false,
      isStriped: true,
      isNarrowed: false,
      isLoading: false,
      hasMobileCards: true
    }
  },
  methods: {
    switchTypeResultRow (row) {
      switch (row.status) {
        case 'win':
          return 'is-success'
        case 'loss':
          return 'is-info'
        default:
          return ''
      }
    }
  }
}
</script>




