// TODO:   JS    
import Vue from 'vue'
import Web3 from 'web3'
import routerAbi from './routerAbi'
import lottoAbi from './lottoAbi'
import tokenAbi from './tokenAbi'

class ETH {
  static install (vue, options) {
    const eth = new ETH()
    Object.defineProperty(Vue.prototype, '$eth', {
      get () { return eth }
    })
  }

  constructor () {
    this._web3 = null
    this._lottoRouter = null
    this._lottoToken = null
    this._lottoContract = null
  }

  routerAddress () {
    return '0x35efabf6e2d970a3bc3fa1de991ec63987c127bc'
  }

  tokenAddress () {
    return '0x2d52ca92b2a3f60ef6dcaa248c4ab1d8a6021e7d'
  }

  getActualLotto () {
    return new Promise((resolve, reject) => {
      this._lottoRouter.methods.actualGoGoLoto().call((err, addr) => {
        if (err) reject(err)
        else resolve(addr)
      })
    })
  }

  async loadLottoContract () {
    try {
      let addr = await this.getActualLotto()
      this._lottoContract = new this._web3.eth.Contract(lottoAbi, addr)
    } catch (err) {
      console.log(err)
    }
  }

  async load () {
    try {
      this._web3 = new Web3('https://mainnet.infura.io')
      this._lottoRouter = new this._web3.eth.Contract(routerAbi, this.routerAddress())
      this._lottoToken = new this._web3.eth.Contract(tokenAbi, this.tokenAddress())
      await this.loadLottoContract()
    } catch (err) {
      console.log(err)
    }
  }

  dailyCounter () {
    return new Promise((resolve, reject) => {
      this._lottoContract.methods.dailyCounter().call((err, counter) => {
        if (err) reject(err)
        else resolve(counter)
      })
    })
  }

  weeklyCounter () {
    return new Promise((resolve, reject) => {
      this._lottoContract.methods.weeklyCounter().call((err, counter) => {
        if (err) reject(err)
        else resolve(counter)
      })
    })
  }

  monthlyCounter () {
    return new Promise((resolve, reject) => {
      this._lottoContract.methods.monthlyCounter().call((err, counter) => {
        if (err) reject(err)
        else resolve(counter)
      })
    })
  }

  annualCounter () {
    return new Promise((resolve, reject) => {
      this._lottoContract.methods.annualCounter().call((err, counter) => {
        if (err) reject(err)
        else resolve(counter)
      })
    })
  }

  async daily (number) {
    return new Promise((resolve, reject) => {
      this._lottoContract.methods.dailyDraws(number).call((err, daily) => {
        if (err) reject(err)
        else resolve(daily)
      })
    })
  }

  async weekly (number) {
    return new Promise((resolve, reject) => {
      this._lottoContract.methods.weeklyDraws(number).call((err, daily) => {
        if (err) reject(err)
        else resolve(daily)
      })
    })
  }

  async monthly (number) {
    return new Promise((resolve, reject) => {
      this._lottoContract.methods.monthlyDraws(number).call((err, daily) => {
        if (err) reject(err)
        else resolve(daily)
      })
    })
  }

  async annual (number) {
    return new Promise((resolve, reject) => {
      this._lottoContract.methods.annualDraws(number).call((err, daily) => {
        if (err) reject(err)
        else resolve(daily)
      })
    })
  }

  async dailyParticipant (drawNumber, number) {
    return new Promise((resolve, reject) => {
      this._lottoContract.methods.getParticipant(0, drawNumber, number).call((err, addr) => {
        if (err) reject(err)
        else resolve(addr)
      })
    })
  }

  async weeklyParticipant (drawNumber, number) {
    return new Promise((resolve, reject) => {
      this._lottoContract.methods.getParticipant(1, drawNumber, number).call((err, addr) => {
        if (err) reject(err)
        else resolve(addr)
      })
    })
  }

  async monthlyParticipant (drawNumber, number) {
    return new Promise((resolve, reject) => {
      this._lottoContract.methods.getParticipant(2, drawNumber, number).call((err, addr) => {
        if (err) reject(err)
        else resolve(addr)
      })
    })
  }

  async annualParticipant (drawNumber, number) {
    return new Promise((resolve, reject) => {
      this._lottoContract.methods.getParticipant(3, drawNumber, number).call((err, addr) => {
        if (err) reject(err)
        else resolve(addr)
      })
    })
  }

  async balanceOf (addr) {
    return new Promise((resolve, reject) => {
      this._lottoToken.methods.balanceOf(addr).call((err, addr) => {
        if (err) reject(err)
        else resolve(this._web3.utils.fromWei(addr))
      })
    })
  }

  isLoaded () {
    return this._web3 !== null
  }

  isAddress (addr) {
    return this._web3.utils.isAddress(addr)
  }

  getBlockNumber () {
    return new Promise((resolve, reject) => {
      this._web3.eth.getBlockNumber((err, blockNumber) => {
        if (err) reject(err)
        else resolve(blockNumber)
      })
    })
  }

  getConfirmations (txHash) {
    return new Promise((resolve, reject) => {
      this.getBlockNumber()
      .then(blockNumber => {
        this._web3.eth.getTransactionReceipt(txHash, (err, receipt) => {
          if (err) reject(err)
          else if (receipt !== null) resolve(blockNumber - receipt.blockNumber)
          else resolve(0)
        })
      })
    })
  }

  sum (first, second) {
    return this._web3.toBigNumber(first).add(second)
  }

  fromWei (amount) {
    return this._web3.utils.fromWei(amount)
  }
}

Vue.use(ETH, {})



